
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'

import vHeader from '@/components/HeaderSearch'
import GoodsList from '@/components/GoodsList'
import OfflineAlert from '@/components/OfflineAlert'

export default {
components: {
Tab,
TabItem,
Swiper,
vHeader,
GoodsList,
OfflineAlert
},
data () {
return {
isPosition: false,             // tab header 
isFromAndroid: false,          // 
isLoading: false,              // 
isShowAlert: false,            // 
loadingText: '...',      // ...
totalPage: 0,                  // 
tabActive: 0,                  // 
tabHeight: 44,                 // tab  44 
carousel: [],                  // 
storeData: {},                 // 
recommendParams: {             // 
supplier_id: this.$route.params.id,
page: 1
},
recommendGoods: [],            // 
uid: null,                    // ID
offlineInfo: {                 // 
address: '',
phone: '',
desc: ''
},
sortName: 'sold_number',              // tab  sold_number()  platform_price() favourable_comment_rate()
platformPriceSortNum: 4,              //    3 4
favourableCommentRateSortNum: 4,      //    3 4
allGoodsParams: {     // 
supplier_id: this.$route.params.id,
page: 1,
'sort[]': 'sold_number:3',
size: 12
},
allGoodsData: []      
}
},
methods: {
androidFun () { // 
window.AndroidWebView.webfinish()
},
onClickTab (index) {
this.tabActive = index
if (index === 0) {

this.tabHeight = 44
this.recommendParams.page = 1     
this.recommendGoods = []      
this.getStoreShopGoods()
} else {

this.tabHeight = 88
this.allGoodsParams.page = 1      
this.allGoodsData = []      
this.getAllGoodsData()
}
},
getStoreShopGoods () {     // 
this.axios.get('/supplier/recommend-second', this.recommendParams, res => {
console.log(res, '')
let data = res.data
this.recommendGoods = this.recommendGoods.concat(data.recommend_second)
this.totalPage = Math.ceil(data.total / 12)     
this.isLoading = false      
})
},
getStoreData () {     // 
this.axios.get('/supplier/index', {supplier_id: this.$route.params.id}, res => {
console.log(res, '')
let data = res.data.index
this.storeData = data
this.carousel = data.carousel.map(item => {
return {
url: '/good-detail/' + item.url,
img: item.image
}
})
this.offlineInfo.address = data.district
this.offlineInfo.phone = data.line_supplier_mobile
this.uid = data.supplier_uid
})
},
getFansData () {      // 
this.axios.get('/supplier/index', {supplier_id: this.$route.params.id}, res => {
this.storeData = res.data.index
})
},
/**
* 
* 
* @param bool
*/
isShow (bool) {
this.isShowAlert = bool
},
getAllGoodsData () {
this.axios.get('/supplier/goods', this.allGoodsParams, res => {
console.log(res, '')
let data = res.data
this.allGoodsData = this.allGoodsData.concat(data.supplier_goods)
this.totalPage = Math.ceil(data.total / 12)     
this.isLoading = false      
})
},
tabHandle (str) {
this.sortName = str
switch (str) {      // 
case 'platform_price':

this.platformPriceSortNum = this.platformPriceSortNum === 4 ? 3 : 4     
this.favourableCommentRateSortNum = 4                                   
this.allGoodsParams['sort[]'] = this.sortName + ':' + this.platformPriceSortNum
break
case 'favourable_comment_rate':

this.favourableCommentRateSortNum = this.favourableCommentRateSortNum === 4 ? 3 : 4     
this.platformPriceSortNum = 4                                                           
this.allGoodsParams['sort[]'] = this.sortName + ':' + this.favourableCommentRateSortNum
break
case 'sold_number':
this.allGoodsParams['sort[]'] = this.sortName + ':' + 3
}
this.allGoodsParams.page = 1      
this.allGoodsData = []      
this.getAllGoodsData()
},
attentionStore () {

let params = {
supplier_id: this.$route.params.id,
status: this.storeData.is_follow === 1 ? 0 : 1
}
this.axios.post('/user-follow/user-follow-shop', params, res => {
window.AndroidWebView.clearCache()
this.getFansData()
})
},
handleScroll () {
let scrollTop = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset     
sessionStorage.setItem('pos', scrollTop)      
let tabTop = document.querySelector('.tab-group').getBoundingClientRect().top     
console.log(tabTop)
this.isPosition = tabTop <= 46      
if (this.isLoading) return    
let node, top, nodeTop
if (this.tabActive === 0) {     //  tab  
node = document.querySelector('.store-goods-item:last-child')     
} else {
node = document.querySelector('.goods-item:last-child')     
}
top = document.documentElement.clientHeight     
nodeTop = node.getBoundingClientRect().top + 100      
console.log(nodeTop, top)
if (nodeTop <= top) {
this.isLoading = true           
if (this.tabActive === 0) {
if (this.recommendParams.page < this.totalPage) {      // 
this.loadingText = '...'
this.recommendParams.page++     
this.getStoreShopGoods()      
} else {
this.loadingText = ''
}
} else {
if (this.allGoodsParams.page < this.totalPage) {      // 
this.loadingText = '...'
this.allGoodsParams.page++     
this.getAllGoodsData()        
} else {
this.loadingText = ''
}
}
}
}
},
created () {

if (this.$route.query.system === 'android') {
this.isFromAndroid = true
}
this.getStoreData()
this.getStoreShopGoods()
},
mounted () {
window.addEventListener('scroll', this.handleScroll)
},
beforeDestroy () {
window.removeEventListener('scroll', this.handleScroll)
}
}



